import { createSlice } from "@reduxjs/toolkit"

const initialState = {
	modalList: [],
}

export const modalSlice = createSlice({
	name: 'modal',
	initialState,
	reducers: {
		addModal: function (state, action) {
			state.modalList.push(action.payload);
		},
		deleteModal: function(state, action) {
			const targetIdx = action.payload;
			state.modalList = state.modalList.filter((item, index) => {
				return index !== targetIdx
			});
			

		},
		resetModal: function(state) {
			state.modalList = [];
		}
	}
})

export const {addModal, deleteModal, resetModal} = modalSlice.actions;
export default modalSlice.reducer;
