import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react'
import { setProfile } from '../store/profileSlice'
//  URL    API .
export const dummyApi = createApi({
  reducerPath: 'dummyApi',
  baseQuery: fetchBaseQuery({ baseUrl: 'http://localhost:3000/' }),
  endpoints: (builder) => ({

	// submitPetInfo
	submitPetInfo: builder.query({
		// 2    .
		async queryFn(_arg, _queryApi, _extraOptions, fetchWithBQ) {
			// get a random user
			const checkNumber = await fetchWithBQ('checkAnimalNumber');
			const medicalQuestion = await fetchWithBQ(`medicalQuestion/DOG`)

			return {data: {checkNumber, medicalQuestion}}
		},

	}),

	//     
	getPetList: builder.query({
		query: () => `petList`,
		providesTags: (result, error, id) => [{type: 'petList', id}],
		transformResponse: (response, meta, arg) => response.data,
		onQueryStarted: async (id, { dispatch, queryFulfilled }) => {
			try {
			  const { data } = await queryFulfilled;
			  //debugger;
			  // `onSuccess` side-effect
			  dispatch(setProfile(data[0]))
			} catch (err) {

			}
		  },
	}),
	//     
	getPetInfoByName: builder.query({
		query: (name) => `petList/${name}`
	}),
	//    
	checkAnimalNumber: builder.query({
		query: ({ id, ...patch }) => ({
			url: `checkAnimalNumber`,
			method: 'POST',
			body: patch,
		}),
	}),
	//   
	createPetInfo: builder.mutation({
		query: ({ id, ...patch }) => ({
			url: `petList`,
			method: 'POST',
			body: patch,
		}),
		invalidatesTags: ['petList'],
		// transformResponse: (response, meta, arg) => response.data,
	}),

	//   
	getMedicalQuestion: builder.query({
		query: (type) => `medicalQuestion/${type}`,
	}),
	//   
	getSearchPet: builder.query({
		query: (type) => `searchPet/${type}`
	}),
	
  }),
})

//     hook 
export const { 
	// useCheckAnimalNumberQuery,			//   (lazy)
	useGetMedicalQuestionQuery,			//   
	useGetSearchPetQuery,				//   
	useGetPetListQuery,					//
	useGetPetInfoByNameQuery,			//
	useCreatePetInfoMutation,			//  
} = dummyApi
