import React, { useState } from "react";
import { useAlert, useBottomSheet, useCustomModal } from "@hooks/useModal";
import styled from 'styled-components'
import Input from "@components/Input";
import { format } from "date-fns";
import { useInput } from "../../hooks/useInput";
import { PageLayout } from "@chac/common/style/sytles";
const StyledContainer = styled(PageLayout)`
	.title {
		text-align: left;
		font-size: 1.4rem;
		font-weight: 400;
	}
	.box {
		border: solid 1px #e2e2e2;
		padding: 20px 15px;
		margin: 15px auto;
	}
	.input-box {
		text-align: left;
		padding: 10px;
    	margin: 15px;
		label {
			font-weight: 400;
			font-size: 1.4rem;
			margin-right: 10px;
		}
		input {
			color: blue;
			background: white;
			padding: 5px 10px;
			border-bottom: solid 1px;
			border-radius: 15px;
		}
		input:read-only{
			color: gray;
			background: #d2d2d2;
		}
	}
	button {
		background: blue;
		color: white;
		padding: 10px;
		border-radius: 20px;
	}
`
/**
 * 
 * @returns 
 */
export default function ModalTest() {
	//  

	// 0.  
	const [showAlert] = useAlert();
	const [showCustomModal] = useCustomModal();	
	// 1. input
	const [{modalTitle, modalContent, name, age}, setValue, onChange, reset] = useInput({
		modalTitle: 'Title ',
		modalContent: 'Content ',
		name: '',
		age: '33'
	})
	const [number, setNumber] = useState(0);


	const btnModalClick = () => {
		showAlert({
			title: modalTitle,
			content: modalContent,
			closeHandler: function (result) {
				console.log(result)
				console.log(modalTitle + "  ");
			},
		});
	};
	// 2. datePicker
	const [date, setDate] = useState(format(new Date(), "yyyy/MM/dd"));

	// 3. Custom Modal
	const btnCustomModalClick = () => {
		// path: "modals/"   ,
		// closeHandler:  , 
		//    key     ex) props
		showCustomModal({
			path: "CustomSampleModal",
			props: {
				name,
				age,
				number
			},
			closeHandler: function (result) {
				if (result == 'cancel') {
					return;
				}
				setNumber(result.number);
				console.log(' >>>>>>>', result)
				console.log(modalTitle + "  ");
			},
		});
	};
	return (
		<StyledContainer>
			<h3> /  </h3>
			<div className="box">
				<h2 className="title">1. input   Alert </h2>
				<div className="input-box">
					<label></label>
					<input type="text" name="modalTitle" value={modalTitle} onChange={onChange} />
				</div>
				<div className="input-box">
					<label></label>

					<input type="text" name="modalContent" value={modalContent} onChange={onChange} />
				</div>
				<button onClick={btnModalClick}>  </button>
			</div>

			<div className="box">
				<h2 className="title">2. datePicker    ()</h2>
				<Input
					type="date"
					value={date}
					setValue={setDate}
					onChange={(val) =>
						showAlert({ title: " ", content: val })
					}
				/>
			</div>
			<div className="box">
				<h2 className="title">3.   </h2>
				<h3 className="sub-title"> </h3>
				<div className="input-box">
					<label></label>
					<input type="text" value={name} name="name" onChange={onChange} />
				</div>
				<div className="input-box">
					<label></label>
					<input type="text" value={age} name="age" onChange={onChange} />
				</div>
				<h3 className="sub-title"> </h3>
				<div className="input-box">
					<label></label>
					<input type="text" value={number} name="number" disabled/>
				</div>
				<button onClick={btnCustomModalClick}>
					   
				</button>
			</div>
		</StyledContainer>
	);
}
