import React from "react";
import styled from "styled-components";
import { useForm } from "react-hook-form";
const StyledContainer = styled.div`
	padding: 15px 10px;
	.input-group {
		margin: 20px;
		label {
			margin-right: 20px;
			font-size: 1.4rem;
			font-weight: 400;
		}
		input {
			border: solid 1px #e2e2e2;
			border-radius: 10px;
			font-size: 1.2rem;
			color: chocolate;
			padding: 10px;
		}
	}
	button {
		background-color: blue;
		padding: 15px 10px;
		width: 100%;
		font-size: 15px;
		color: white;
		border-radius: 15px;
	}
	small {
		color: red;
	}
`;

export default function InputTest() {
	const {
		register, 
		handleSubmit,
		formState: { isSubmitting, isDirty, errors },
	}  = useForm();
	return (
		<StyledContainer>
			<h1>input  </h1>
			<form onSubmit={handleSubmit((data) => alert(JSON.stringify(data)))}>
				<div className="input-group">
					<label></label>
					<input type="text" name="name" {...register('name', {
						required: "  .",
						minLength: {
							value: 2,
							message: "2  .",
						},
					})}/>
				</div>
				{errors.name && <small>{errors.name.message}</small>}

				<div className="input-group">
					<label></label>
					<input type="tel" name="age" {...register('age', {
						required: "  .",
						onChange: (e) => {	
							//   
							const value = e.target.value;
							e.target.value = Number(value) ? value : value.slice(0, -1)
						},
						validate:  {
							checkAge: (value) => value < 19 ? " 19  ." : true
						}
					})}/>
				</div>
				{errors.age && <small>{errors.age.message}</small>}

				<div className="input-group">
					<label></label>
					<input type="text"   {...register('kind', {    
						required: ' .',
						// required: true,
						// boolean    , input value     errors       .
						minLength: { // value  
							value: 3,
							message: '3  .', //  
						},
						pattern: { // input  
							value: /^[A-za-z0-9-]{3,10}$/,
							message: ' :  ,   , ', //  
						},
					})} />
				</div>
				{errors.kind && <small>{errors.kind.message}</small>}

				<div className="input-group">
					<input {...register("Developer", { required: true })} type="radio" value="Y" />
					<input {...register("Developer", { required: true })} type="radio" value="N" />
				</div>


				<button type="submit" disabled={isSubmitting}>  </button>
			</form>
		</StyledContainer>
	);
}
