import { Modal } from "@chac/common/components/Modal";
import PropTypes from 'prop-types'
import React, { Suspense, lazy } from "react";
import styled from 'styled-components'
import { useDispatch, useSelector } from "react-redux";
import { deleteModal } from "./store/modalSlice";
const Container = styled.div`
	position: fixed;
	top:0;
	width: 100%;
	height: 100%;
`
export default function ModalManager(props) {
	const dispatch = useDispatch();
	const modalList = useSelector((state) => state.modal.modalList);
	if (modalList.length === 0) {
		return null;
	}
	return (
		<Suspense fallback={<h3>.</h3>}>
			<Container>
				{
					modalList.map((modalData, index) => {
						//   closeHandler  &  
						const closeModal = (result) => {
							// props  closeHandler  
							if (modalData.data.closeHandler) {
								modalData.data.closeHandler(result);
							}
							//  
							dispatch(deleteModal(index))
						};	
						if (modalData.type === 'custom') {
							//   
							const CustomModal = lazy(()=> import(`./modals/${modalData.data.path}`));
							return <CustomModal key={index} {...modalData} close={ closeModal }/> 
						} else {
							//   (title, text  )
							return (<Modal key={index} {...modalData} close={ closeModal }/> )
						}
					})
				}	
			</Container>
		</Suspense>
	);
}


ModalManager.propTypes = {
	modalList: PropTypes.arrayOf(PropTypes.shape({
		data: PropTypes.object.isRequired,
		closeHandler: PropTypes.func,
		type: PropTypes.oneOf(["alert", "confirmAlert", "popup", "bottomSheet"]),
	}))
}
