import { useDispatch } from 'react-redux'
import { addModal, deleteModal } from '../store/modalSlice';
/**
 * type = [ alert | confirmAlert | custom ]
 * data
 */
function useModal(props) {
	const dispatch = useDispatch();
	let modalProp = props;
	const close = () => {
		dispatch(deleteModal(modalProp));
	}
	// Open
	const open = (props) => {
		modalProp = {
			...modalProp, 
			data: props
		}
		dispatch(addModal(modalProp));
	}

	return [open, close];
}

export function useAlert(data) {
	return useModal({
		type: "alert",
		data
	})
}
/**
 * 
 * @param {Object} data 
 * @returns 
 */
export function useConfirmAlert(data) {
	return useModal({
		type: "confirmAlert",
		data
	})
}

export function useCustomModal(data) {
	return useModal({
		type: "custom",
		data
	})
}
