import React, { useEffect, useState } from "react";
import styled from 'styled-components'
import { useGet } from "@hooks/useFetch";
import {useGetPokemonListQuery} from '../../query/pokemon_dummy'
const StyledPokeView = styled.ul`
	li {
		font-size: 18px;
		padding: 10px;
		margin: 3px auto;
		width: 200px;
		list-style: none;
		background-color: green;
		border-radius: 10px;
		color: white
	}
`
function DetailView(props) {
	const detailData = useGet(props.url);
	const Loading = () => (<h3>..</h3>)
	return (
		!detailData ? 
		<Loading /> : 
		(
			<div>
				<img src={detailData.sprites.front_default}></img>
			</div>
		)
	)
}

function PokeListView ({list, setDetailUrl}) {

	return (
		<StyledPokeView>
			{
				list.map((item, index) => (
					<li key={index} onClick={()=> setDetailUrl(item.url)}>{item.name}</li>
				))
			}
			
		</StyledPokeView>
	)
}
export default function FetchTest() {
	const [detailUrl, setDetailUrl] = useState('');
	// const data = useGet('https://pokeapi.co/api/v2/pokemon');
	const { data } = useGetPokemonListQuery();
	console.log(data)
	return (
		<div>
			<h2>FetchTest</h2>
			{
				detailUrl && <DetailView url={detailUrl} /> 
			}

			{
				data?.results && <PokeListView list={data.results} setDetailUrl={setDetailUrl} />
			}

			
		</div>
	);
}
