import {configureStore} from '@reduxjs/toolkit';
import modalReducer from './modalSlice';
import chacpetReducer from './chacpetSlice';
import profileReducer from './profileSlice';
import { pokeDummyApi } from '../query/pokemon_dummy'
import { dummyApi } from '../query/dummy'
export const store = configureStore({
	reducer: {
		modal: modalReducer,
		profile: profileReducer,
		chacpet: chacpetReducer,
		[pokeDummyApi.reducerPath]: pokeDummyApi.reducer,
		[dummyApi.reducerPath]: dummyApi.reducer
		
	},
	middleware: getDefaultMiddleware => getDefaultMiddleware({ 
		serializableCheck: false 
	}).concat([
		pokeDummyApi.middleware,
		dummyApi.middleware,
	])
	// ,  ,    rtk-query   api  
})
