import React, { Suspense, lazy, useEffect } from 'react'
import { Route, Routes, useLocation } from 'react-router-dom';
import Test from './pages/Test';
import { useDispatch } from 'react-redux';
import { updateCurPageName } from './store/chacpetSlice';
/**
	Main			
	AddPet		   	 
	AnalysisResult 	 
	Counter			(//)
	CounterResult	 (//)
	MedicalQuestion	
	MedicalQuestionResult  
 */
export default function PageManager() {
	const Main = lazy(() => import('./pages/Main'))
	const TotalLog = lazy(() => import('./pages/TotalLog'))
	const PetRegister = lazy(() => import('./pages/PetRegister'))
	const AnalysisResult = lazy(() => import('./pages/AnalysisResult'))
	const Counter = lazy(() => import('./pages/Counter'))
	const CounterResult = lazy(() => import('./pages/CounterResult'))
	const MedicalQuestion = lazy(() => import('./pages/MedicalQuestion'))
	const MedicalQuestionResult = lazy(() => import('./pages/MedicalQuestionResult'))

	const location = useLocation();
	const dispach = useDispatch();

	useEffect(() => {
		
		let pageName = ( inPath => {
			let result = 'main';
			if ( inPath !== '/' ) {
				const arr = inPath.split('/');
				result = arr[ arr.length - 1 ]
			}
			return result;
		})(location.pathname);
	
		dispach(updateCurPageName(pageName));
	
	  return () => {
		
	  }
	}, [location])
	

	
	
  	return (

		
		<Suspense fallback={"...."}> 
			<Routes>
				{/*  */}
				<Route path='/' 					element={<Main />} />
				{/*   */}
				<Route path="/petRegister" 				element={<PetRegister />} />
				{/*   */}
				<Route path="/totalLog" 				element={<TotalLog />} />
				{/*  */}
				<Route path="/analysisResult" 		element={<AnalysisResult />} />
				{/*  */}
				<Route path="/counter/:kind" 				element={<Counter />} />
				<Route path="/counterResult" 		element={<CounterResult />} />
				{/*  */}
				<Route path="/medicalQuestion" 		element={<MedicalQuestion />} />
				<Route path="/medicalQuestionResult" element={<MedicalQuestionResult />} />
				
				{/*   */}
				<Route path='/test/*' 			element={<Test />} />
			</Routes>
		</Suspense>
  	)
}
