import { SHeader } from "../style/styles";
import { useNavigate } from 'react-router-dom';
import { useState } from "react";
import { useSelector } from "react-redux";


export default function Header(){

    const title = useSelector( state => state.chacpet.currentPageNameKo )
    const navigate = useNavigate();
    const prevHandler = () => {
        navigate('/');
    }
    return(
        <SHeader>
            <div className="btn-dir" onClick={prevHandler}>
                <img src={'./image/icon-arrow-left-24.svg'} className="icon_arrow_left_24"></img>
            </div>
            <p className="title">{title}</p>
            <div className="layout-blank"></div>
        </SHeader>
    )
}