import React, { useState } from 'react'
//import Calendar from 'react-calendar'
import {format} from 'date-fns'
//import 'react-calendar/dist/Calendar.css'
import { useToggle } from '../hooks/useToggle'

export default function Input(props) {
	const type = props.type;
	
	switch (type) {
		case 'date':	return <InputDate {...props}/>
		default:		return (<div>Input</div>);
	}
}

//  BottomSheet 
/**
 * DatePicker
 * TODO:
 * 1.     
 * 2.   props 
 * 		- state
 * 		- setState
 * 		- onChange: DatePicker      
 */
function InputDate({value, setValue, onChange}) {
	const [datePickerState, toggleDatePicker] = useToggle();
	//    
	const _onChange = (value) => {
		const dateStr = format(value,'yyyy/MM/dd');
		if (onChange) {
			onChange(dateStr);
		}
		setValue(dateStr);
		toggleDatePicker();
	}
	
	return (
		<div>
			DatePicker
			<input readOnly value={value} onClick={toggleDatePicker}></input>
			{/* { 	datePickerState && 
				<Calendar value={value} onChange={_onChange}></Calendar>
			} */}
		</div>
	)
}
