import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    currentPageName: 'Main',
    currentPageNameKo: ''
}

const pageNameKo = {
    'main': '!',
    'medicalQuestion': '',
    'medicalQuestionResult': '',
    'analysisResult': '',
    'heart': '',
    'breath': '',
    'TotalLog': '',
    'petRegister': ''

}

export const chacpetSlice = createSlice({
    name: 'chacpet',
    initialState,
    reducers: {
        updateCurPageName: (state, action) => {
            state.currentPageName = action.payload;
            state.currentPageNameKo = pageNameKo[action.payload];
        }
    }
})

export const { updateCurPageName } = chacpetSlice.actions;

export default chacpetSlice.reducer;
