import React, { lazy } from "react";
import PropTypes from "prop-types";
import styled from "styled-components";
import { Button } from "./Button";

const ModalContainer = styled.div`
	position: fixed;
	z-index: 100000;
	top: 0;
	/* background: rgba(0, 0, 0, 0.75); */
	width: 100%;
	height: 100%;
`;
const StyeldDimmed = styled.div`
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0px;
	background: rgba(0, 0, 0, 0.75);
	z-index: 100000;
`;
const StyledBottomSheet = styled.div`
	position: absolute;
	bottom: 0;
	width: 100%;
	background: white;
	max-height: 70%;
	border-radius: 2.4rem 2.4rem 0 0;
	z-index: 100001;
	.head {
		margin: 0 auto;
		padding: 0.8rem 0 3rem;
		text-align: center;
		button{
			width: 4.8rem;
			height: 0.3rem;
			font-size: xx-small;
			color: transparent;
			border: none;
			display: inline-block;
			background: #d9dadc;
			border-radius: 0.15rem;
		}
	}
	.content {
		max-height: 70vh;
		overflow: hidden scroll;
		padding: 0 2.4rem 4rem;
	}
	.footer {
		padding: 0 2.4rem 2.4rem;
	}
`;
const StyledAlert = styled.div`
	background: white;
	padding: 25px 10px;
    width: 70%;
    border: 1px solid gray;
    border-radius: 10px;
    z-index: 100001;
    position: absolute;
    bottom: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
`
const AlertTemplate = ({data, close}) => {
	const { title, content = " " } = data;
	return (
		<StyledAlert>
			{title && (<div className="header">{title}</div>)}
			<div className="content">{content}</div>
			<div className="footer"> <Button onClick={close}></Button> </div>
		</StyledAlert>
	);
};
const BottomSheetTemplate = ({data, close, children}) => {
	return (
		<StyledBottomSheet>
			<div className="head">
				<button></button>
			</div>
			{children}
		</StyledBottomSheet>
	)
}
//  
export function Modal(props) {
	//   Type 
	return (
		<ModalContainer>
			<StyeldDimmed onClick={() => props.close('cancel')} />
			{
				props.type === 'alert' ? (<AlertTemplate {...props} />) :
				props.type === 'bottomSheet' ? (<BottomSheetTemplate {...props} />) :
				null
			}
		</ModalContainer>
	);
}

// 
Modal.defaultProps = {
	type: "alert",
};
// props 
Modal.propTypes = {
	data: PropTypes.object,
	closeHandler: PropTypes.func,
	type: PropTypes.oneOf(["alert", "confirmAlert", "popup", "bottomSheet", "custom"]),
};
